<template>
  <div id="app">
    <div class="game-holder" ref="gh">
      <template v-if=isReady>
      <Game v-bind:dimensions="getGameDimensions()"/>
      </template>
    </div>
    <div class="controls-holder" ref="ch">
      <div v-on:click="togglePlayState()" class="button-wrapper">
        <i v-if="!isPlaying" class="icon material-icons not-selectable">play_arrow</i>
        <i v-if="isPlaying" class="icon material-icons not-selectable">pause</i>
      </div>
      <div v-on:click="updateSpeed()" class="button-wrapper">
        <i class="icon material-icons not-selectable">speed</i><span class="icon-text">{{ this.speeds[this.currentSpeedIndex]}}</span>
      </div>
      <div v-on:click="reset(false)" class="button-wrapper">
        <i class="icon material-icons not-selectable">refresh</i>
      </div>
    </div>
  </div>
</template>

<script>
import Game from './components/Game.vue'
import { bus } from './main';

export default {
  name: 'App',
  data: function(){
    return {
      isReady: false,
      isPlaying: false,
      speeds: [0.5, 1, 2, 4, 8, 16],
      currentSpeedIndex: 2
    }
  },
  components: {
    Game
  },
  methods: {
    getGameDimensions: function(){
      return {
        width:this.$refs.gh.offsetWidth,
        height:this.$refs.gh.offsetHeight
      }
    },
    togglePlayState(){
      this.isPlaying = !this.isPlaying
      bus.$emit('play-pause', { isPlaying: this.isPlaying })
    },
    updateSpeed(){
      this.currentSpeedIndex = (this.currentSpeedIndex + 1) % this.speeds.length
      bus.$emit('update-fps', { fps: this.speeds[this.currentSpeedIndex] })
    },
    reset(isAlive){
      bus.$emit('reset', {isAlive:isAlive });
    }
  },
  mounted: function(){
    this.isReady = true;
  }
}
</script>

