<template>
  <div>
    <div id="game"></div>
  </div>
  
</template>

<script>
import { SVG } from '@svgdotjs/svg.js'
import { gsap } from 'gsap';
import { bus } from '../main';
//TODO: Document this -- import '@svgdotjs/svg.draggable.js'
export default {
  name: 'Game',
  props: [
    'dimensions'
  ],
  mounted: function(){
    console.log(this.dimensions);
    let paper = SVG().addTo('#game').size(this.dimensions.width, this.dimensions.height)

    let nLesser = 25;
    let lesserDimension = Math.min(this.dimensions.width, this.dimensions.height);
    let side = lesserDimension / (nLesser + 1);

    let nX = Math.floor(this.dimensions.width / side) - 1;
    let nY = Math.floor(this.dimensions.height / side) - 1;

    let paddingX = (this.dimensions.width - nX * side) / 2;
    let paddingY = (this.dimensions.height - nY * side) / 2;

    let grid = [];




    let correctIndex = function(index, indexMax) {
      // return index % indexMax; //Modulo doesn't work as expected
      return index < 0 ? index + indexMax : index % indexMax;
    }

    let countLivingNeighbors = function(cell) {
      let count = 0;
      let iCell = cell.i;
      let jCell = cell.j;
      for(let i = iCell - 1; i < iCell + 2; i++){
        for(let j = jCell - 1; j < jCell + 2; j++) {
          if(i !== iCell || j !== jCell){
            let iNeighbor = correctIndex(i, nX);
            let jNeighbor = correctIndex(j, nY);
            let neighborCell = grid[iNeighbor][jNeighbor];
            count = neighborCell.wasAlive ? count + 1 : count;
          }
        }
      }
      return count;
    }

    let assignClass = function(square, isAlive){
      if(isAlive){
        square.removeClass('dead'); //Q: What happens if 'dead' is not present?
        square.addClass('alive');
      } else {
        square.removeClass('alive');
        square.addClass('dead')
      }
      return square;  
    }

    let setCells =function(isAlive) {
      for(let i = 0; i < nX; i++){
        for(let j = 0; j < nY; j++) {
          let cell = grid[i][j];
          cell.isAlive = isAlive;
          cell.wasAlive = null;
          assignClass(cell.square, cell.isAlive);
        }
      }
    }







    for(let i = 0; i < nX; i++) {
      let row  = []
      for(let j = 0; j < nY; j++) {
        let square = paper.rect(0.9 * side, 0.9 * side ).move(paddingX + i * side, paddingY + j * side);
        square.on('mousedown', function(){
          cell.isAlive = !cell.isAlive;
          assignClass(square, cell.isAlive);
        });
        let cell = {
          i,
          j,
          isAlive:false, // not needed (see setCells)
          wasAlive:null, // not needed
          square
        }
        row.push(cell);
      }
      grid.push(row);
    }
    console.log(grid);
    
    setCells(false);


    let isPlaying = false;
    bus.$on('play-pause', (data) => {
      isPlaying = data.isPlaying;
    });

    let fps = 1;
    bus.$on('update-fps', (data) => {
      fps = data.fps;
    });

    bus.$on('reset', (data) => {
      setCells(data.isAlive);
    })




    // Conway's Rules
    // ==============
    // 1) Any live cell with two or three live neighbours survives.
    // 2) Any dead cell with three live neighbours becomes a live cell.
    // 3) All other live cells die in the next generation. Similarly, all other dead cells stay dead.
    let previousFps = fps;
    let animationStage = function(time){
      if(isPlaying){
        if(previousFps !== fps){
          console.log(fps)
          gsap.ticker.fps(fps);
          previousFps = fps;
        }
        //isAlive ==> wasAlive
        for(let i = 0; i < nX; i++){
          for(let j = 0; j < nY; j++){
            let cell = grid[i][j];
            cell.wasAlive = cell.isAlive;
          }
        }
        for(let i = 0; i < nX; i++) {
          for(let j = 0; j < nY; j++) {
            let cell = grid[i][j];
            let livingNeighbors = countLivingNeighbors(cell);
            if(cell.isAlive){
              if(livingNeighbors < 2 || livingNeighbors > 3){
                cell.isAlive = !cell.wasAlive;
                assignClass(cell.square, cell.isAlive);
              }
            } else {
              if(livingNeighbors === 3){
                cell.isAlive = !cell.wasAlive;
                assignClass(cell.square, cell.isAlive);
              }

            }
          }
        }
      }
    }
    
    gsap.ticker.fps(fps);
    gsap.ticker.add(animationStage)

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.alive {
  fill:white;
}
.dead {
  fill:darkgray;
}
</style>
